import React, { Component } from 'react'
import Cookies from 'universal-cookie'
import Axius from 'axios'
var vars = require('./GlobalVars')

class MainHeader extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             showButtons: true,
             showUserButtons: false,
             userName: ""
        }
        this.openMenu = this.openMenu.bind(this)
        this.toDefaultMenu = this.toDefaultMenu.bind(this)
        this.cookies = new Cookies()
        this.logout = this.logout.bind(this)
    }
    
    componentDidMount(){
            this.props.pointerToAssign("header",this)
            

            if(this.cookies.get('session') !== null && this.cookies.get('session') !== undefined){
                let form_data = new FormData()

                let body = {"IDSession": this.cookies.get('session'), "user" : this.cookies.get('user')}
                
                for(var key in body){
                    form_data.append(key, body[key])
                }
                var pointerClass = this

                Axius.post(vars.default.APIServiceURL + "/checklogin", form_data).then(response => {
                    console.log(response)    
                    if(typeof(response.data) != "string" && response.data["IDSession"] === this.cookies.get('session')){
                        pointerClass.setState({
                            showButtons: false,
                            showUserButtons: true,
                            userName: pointerClass.cookies.get('user')
                        })
                        pointerClass.props.parent.setState({contentPage: "games"})
                    } else{
                        this.cookies.remove('session')
                        this.cookies.remove('user')
                    }
                    
                }).catch(error => {
                    console.log("Error : " + error)
                })
            }else{
                this.cookies.remove('session')
                this.cookies.remove('user')
            }
    }

    render() {
        return (
            <header>       
                <h1 id="mainHeaderTitle">
                    Power Bet
                </h1>
                {this.renderButtons(this.state.showButtons)}
                {this.renderUserButtons(this.state.showUserButtons)}
            </header>
        )
    }

    renderButtons(showButtons){
        if(showButtons){
            return(
                <div id="userIntialInput">
                    <button onClick={()=>this.openMenu("login")} id="userLogin" className="mainButton">
                        Login
                    </button>
                    <button onClick={()=>this.openMenu("sign")} id="userSignIn" className="mainButton">
                        Sign In
                    </button>
                </div>      
            )
        }else{
            return(<div></div>)
        }
    }

    renderUserButtons(showButtons){
        if(showButtons){
            return(
                <div id="userIntialInput">
                    <div id="userTag">
                        {this.state.userName}
                    </div>
                    <button onClick={()=>this.openMenu("account")} id="userLogin" className="mainButton">
                        Account
                    </button>
                    <button onClick={()=>this.logout()} id="userSignIn" className="mainButton">
                        Logout
                    </button>
                </div>      
            )
        }else{
            return(<div></div>)
        }
    }

    openMenu(newMenu){
        this.setState({showButtons: false, showUserButtons: false})
        this.props.changeMenu(newMenu)
    }

    logout(){
        let form_data = new FormData()

                let body = {"IDSession": this.cookies.get('session'), "user" : this.cookies.get('user')}
                
                for(var key in body){
                    form_data.append(key, body[key])
                }
                var pointerClass = this

                Axius.post(vars.default.APIServiceURL + "/logout", form_data).then(response => {
                    console.log(response)    
                    if(typeof(response.data) == "string" && response.data.includes("Session removed")){
                        
                        pointerClass.props.parent.callAlert("normal", "Logout Success!")
                        
                    }else{
                        pointerClass.props.parent.callAlert("warning", "Logout Problem!")
                    } 
                }).catch(error => {
                    console.log("Error : " + error)
                })
                this.cookies.remove('session')
                this.cookies.remove('user')
                this.setState({
                    showButtons: true,
                    showUserButtons: false
                })
                this.props.parent.setState({contentPage: "logo"})
    }

    toDefaultMenu(){
        this.props.changeMenu("")
    }
}

export default MainHeader
