import React, { Component } from 'react'
import Cookies from 'universal-cookie'
import Axius from 'axios'
import ElementEvent from './ElementEvent'
import AccountBalance from './AccountBalance'
var vars = require('./GlobalVars')

class EventsWelcome extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             games: [],
             userMoney: 0
        }

        this.teams = {
                'Exortia'   : 1,
                'Memorum'   : 2,
                'Bilan'     : 3,
                'Karkatsu'  : 4,
                'Melogan'   : 5,
                'Xartam'    : 6,
                'Calug'     : 7,
                'Pirur'     : 8,
                'Healyb'    : 9,
                'Varvaro'   : 10
            }

            this.games = []
            this.gamesName = []

            this.renderGridItems = this.renderGridItems.bind(this)

            this.cookies = new Cookies()
        }
    
    renderGridItems(){
        
        Axius.get(vars.default.APIServiceURL + "/games").then(response => {
            console.log(response)
           Object.entries(response.data.games).map((elem)=>{
                //console.log(elem)
                this.games.push(elem[1])
                this.gamesName.push(elem[0])
            })   
            console.log(this.games)
             
            this.setState({
                games: this.games
            })
            
        }).catch(error => {
            console.log("Error : " + error)
        })
    }

    componentDidMount(){
        this.renderGridItems()

        var viewport = document.querySelector('meta[name="viewport"]');

        if ( viewport ) {
           viewport.content = "initial-scale=0.5";
          // viewport.content = "width=1200";
        }
    }

    


    render() {
        return (
            <div>
                <AccountBalance name="Account Balance" textClass="account-balance" parent={this}></AccountBalance>
                <div className="gridGames">
                    {this.state.games.map( (elem, index) => { 
                            return( 
                                   <ElementEvent gameName={this.gamesName[index]} parent={this.props.parent} key={index} team1={elem.team1} team2={elem.team2} team1value = {this.teams[elem.team1]} team2value={this.teams[elem.team2]}>  
                                   </ElementEvent>
                                )
                        } )}
                </div>
            </div>
        )
    }
}

export default EventsWelcome
