import React, { Component } from 'react'
import Title from './Title'
import $ from 'jquery'
import Axius from 'axios'
import Cookies from 'universal-cookie'
var vars = require('./GlobalVars')


class SignIn extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
        }

        this.assignForClose = this.assignForClose.bind(this)
        this.closeThisMenu = this.closeThisMenu.bind(this)
        this.showHeaderButtons = this.showHeaderButtons.bind(this)
        this.headerThis = null
        this.trySignUp = this.trySignUp.bind(this)

        this.cookies = new Cookies()

    }
    
    componentDidMount(){
        this.props.pointerToAssign("aboveMenu",this) 
}

    render() {
        return (
            <div id="aboveWindow" className="logIntoService combMoveFadeDown">
                <Title title="Sign"></Title>
                <form >
                    <div className="inputLog">
                        <input className="user" type="text" name="" required="" autoComplete="off"/>
                        <label>Username</label>
                    </div>
                    <div className="inputLog"> 
                        <input className="mail" type="text" name="" required="" autoComplete="new-password"/>
                        <label>Email</label>
                    </div>
                    <div className="inputLog">
                        <input className="password" type="password" name="" required="" autoComplete="new-password"/>
                        <label>Password</label>
                    </div>

                    <button onClick={this.trySignUp} type="button" id="signInProcess"className="mainButton">
                        Sign In
                    </button>
                    <div onClick={()=>this.closeThisMenu(true)} className="close"></div>
                </form>
            </div>
        )
    }

    trySignUp(){

        let email = $("#aboveWindow input.mail").val()
        var user = $("#aboveWindow input.user").val()
        let password = $("#aboveWindow input.password").val()

        
        let body = {"user": user, "email": email, "password": password}
        let form_data = new FormData()

        for(var key in body){
            form_data.append(key, body[key])
        }
        var pointerClass = this
       
        if(user.includes("@") === false && user.includes(".") === false && user.includes('"') === false &&
            user.includes("'") === false){
            if(email.includes('"') === false && email.includes("'") === false){
                if(email.includes("@") === true && email.includes(".") === true){
                    if(password.includes('"') === false && password.includes("'") === false){
                        this.tryAPISignUp(form_data, pointerClass, user)
                    } else{
                        this.props.parent.callAlert("warning", "Password has unsuported symbols:\n \" '")
                    }
                }else{
                    this.props.parent.callAlert("warning", "Email is invalid")
                }
            }else{
                this.props.parent.callAlert("warning", "Email has one of:\n \" '")    
            }
        }else{
           
            this.props.parent.callAlert("warning", "User name has one of:\n. @ \" '")
        }
        
    }


    tryAPISignUp(form_data, pointerClass, user){
        Axius.post(vars.default.APIServiceURL + "/signup", form_data).then(response => {
            console.log(response)    
            if(typeof(response.data) == "string" && response.data.includes("User already exists")){
                pointerClass.props.parent.callAlert("warning", "User Already Exists!")
            }else if(typeof(response.data["IDSession"]) == "string"){
                pointerClass.props.parent.callAlert("normal", "Sign Success!")
                let cookies = new Cookies()
                cookies.set('session', response.data["IDSession"])
                cookies.set('user', user)
                console.log(cookies.get('session'))
                pointerClass.closeThisMenu(false);
                pointerClass.props.parent.setState({contentPage: "games"})
            } else{
                pointerClass.props.parent.callAlert("warning", "Someting went wrong on sign!")
            }
            
        }).catch(error => {
            console.log("Error : " + error)
            pointerClass.props.parent.callAlert("warning", "Sign or Network Failed!")
        })
    }

    assignForClose(pointer){
        this.headerThis= pointer
    }

    showHeaderButtons(showLoginButtons){
        if(showLoginButtons){
            this.headerThis.setState({showButtons: true})
        }else{
            this.headerThis.setState({showUserButtons: true, userName: this.cookies.get('user')})
        }
        this.headerThis.toDefaultMenu()
    }

    closeThisMenu(showLoginButtons){
        var pointerClass = this;
        $("#aboveWindow").removeClass("combMoveFadeDown");
        $("#aboveWindow").addClass("combMoveFadeUp")
        setTimeout(function(){
            pointerClass.showHeaderButtons(showLoginButtons)
        }, 550)
    }
}

export default SignIn
