import React, { Component } from 'react'
import AccountInfoGameRow from './AccountInfoGameRow'

export class GamesInfo extends Component {
    render() {
        return (
            <div className="accountInfoWinGames">
                    <table>
                        <thead>
                            <tr>
                                <th>Team 1</th>
                                <th>Team 2</th>
                                <th>Money</th>
                            </tr>
                        </thead>
                        <tbody>
                           
                            {Object.keys(this.props.games).map( (key, index) => { 
                                        return( 
                                            <AccountInfoGameRow parent={this.props.parent} key={index} team1={this.props.games[key]["team1"]} team2={this.props.games[key]["team2"]} money={this.props.games[key]["moneyEarned"]} gameName={key}>
                                            </AccountInfoGameRow>
                                        )
                                    }
                                )
                            }
                        </tbody>
                    </table>
                </div> 
        )
    }
}

export default GamesInfo
