import React, {Component} from 'react'
import Unity, {UnityContext} from 'react-unity-webgl'
import "./CSS/Unity.css"
import BetTimer from './BetTimer'
import $ from 'jquery'
import Axius from 'axios'
import Cookies from 'universal-cookie'
import AccountBalance from './AccountBalance'
var vars = require('./GlobalVars')

var unityComponent = "";
var gameIsSetup = false;

class UnityComp extends Component{
    
    
    constructor(props){
        super(props)
        
        

        this.unityContext = new UnityContext({
            loaderUrl:      vars.default.unityRoute + "PowerBetG/Build/PowerBetG.loader.js",
            dataUrl:        vars.default.unityRoute + "PowerBetG/Build/PowerBetG.data",
            frameworkUrl:   vars.default.unityRoute + "PowerBetG/Build/PowerBetG.framework.js",
            codeUrl:        vars.default.unityRoute + "PowerBetG/Build/PowerBetG.wasm"
        });

        this.state = {
            betMoney : 0,
            betTeam : ""    
        }

        this.timerCallerComponent = null
        this.isGameOver = false
        this.cookies = new Cookies()
        this.teams = [this.props.teamName1, this.props.teamName2]
        unityComponent = this.unityContext
        this.selectedTeam = ""
        this.stage = 1
        this.account = null

        this.callTimer = this.callTimer.bind(this)
    }

    callTimer(time){
        
        this.enableBet()
        this.timerCallerComponent.useTimer(time, true, this.disableBet)
        
    }

    enableBet(){
        $("#betForm").removeClass("betState")
        $("#betForm select").removeClass("betState")
    }

    disableBet(){
        $("#betForm").addClass("betState")
    }

    componentDidMount(){

        var pointerClass = this

        this.unityContext.on("callTest", function(){
            console.log("React reacted...")

            setTimeout(function() {
                console.log("Try communication Unity...")
                console.log("this : " + unityComponent)
                unityComponent.send(
                    "Connector", 
                    "FinishTestCall"
                  )
            }, 2000)
        });

        this.unityContext.on("loaded", () => {
            // Now we can for example hide the loading overlay.
            setTimeout(function(){
                console.log("Start game...")
                if(gameIsSetup === false){
                    gameIsSetup = true;
                    unityComponent.send("Connector","SetupGame", pointerClass.props.team1 + "/" + pointerClass.props.team2 + "/" + pointerClass.teams[0] + "/" + pointerClass.teams[1]);
                    pointerClass.callTimer(20)
                }
            }, 2000)     
            var viewport = document.querySelector('meta[name="viewport"]');

            if ( viewport ) {
            viewport.content = "initial-scale=0.4";
            // viewport.content = "width=1200";
            } 

            pointerClass.props.parent.unityDoneLoading();
        });

        this.unityContext.on("feedbackWinning", function(winningTeamAndRound){
            let values = winningTeamAndRound.split("_")
            console.log("Round: " + values[1] + "\n Team winning: " + values[0] + "\n Team Name: " + pointerClass.teams[values[0]-1]);
            console.log("Player won that round : ")
            console.log(pointerClass.selectedTeam === pointerClass.teams[values[0]-1])
            if(pointerClass.cookies.get('session') !== null && pointerClass.cookies.get('session') !== undefined  && pointerClass.selectedTeam !== ""){
    
                pointerClass.setState({
                    betTeam : "",
                    betMoney : 0
                });

                let session = pointerClass.cookies.get('session')
                let moneyRule = (pointerClass.selectedTeam === pointerClass.teams[values[0]-1])? 0.2 : -1;
                let body = {
                    "IDSession": session,
                    "money": moneyRule,
                    "stage": pointerClass.stage
                }
    
                let form_data = new FormData()
    
                for(var key in body){
                    form_data.append(key, body[key])
                }
    
                Axius.post(vars.default.APIServiceURL + "/calculatebetstage", form_data).then(response => {
                    console.log(response) 
                    pointerClass.account.renderAcountBalance()   
                }).catch(error => {
                    console.log("Error : " + error)
                })
            }

            pointerClass.selectedTeam=""
            pointerClass.stage ++
            setTimeout(function(){
                if(pointerClass.isGameOver !== true){
                    pointerClass.callTimer(15)
                }
            },10)
        })

        this.unityContext.on("gameover", function(){
                console.log("Game is over...")
                pointerClass.isGameOver = true;
                setTimeout(() => {
                    pointerClass.props.parent.setState({ 
                        contentPage: "games"
                    })  
                    
                }, 3000)
                
        })
    }

    verifyNumeric(text){
        if(this.isFloat(parseFloat(text.target.value[text.target.value.length-1])) !== true && this.isInt(parseInt(text.target.value[text.target.value.length-1])) !== true &&
        (text.target.value[text.target.value.length-1] !== "." || text.target.value.split(".").length > 2)){
            text.target.value = text.target.value.substr(0, text.target.value.length - 1)
           
        }else if(text.target.value[text.target.value.length-1] === "."){
            text.target.value = text.target.value+"50"
        }   
    }

    bet(){
        if(this.cookies.get('session') !== null && this.cookies.get('session') !== undefined  && $("#betForm input").val().replace(" ", "") !== ""){
            
            let moneyToBet = $("#betForm input").val()
            let teamToBet = $("#betForm select").val()

            this.selectedTeam = teamToBet

            $("#betForm input").val("")
            $("#betForm select").addClass("betState")


            console.log(teamToBet + " - " + moneyToBet)

            var pointerClass = this

            this.setState({
                betTeam : teamToBet,
                betMoney : this.state.betMoney + parseFloat(moneyToBet)
            });

            let session = this.cookies.get('session')
            let body = {
                "IDSession": session,
                "money": moneyToBet,
                "stage": pointerClass.stage
            }

            let form_data = new FormData()

            for(var key in body){
                form_data.append(key, body[key])
            }

            Axius.post(vars.default.APIServiceURL + "/newbetgame", form_data).then(response => {
                console.log(response)    
            }).catch(error => {
                console.log("Error : " + error)
            })
        }
    }

    render(){
        return( 
            <div>
                <div id="unityLocation">
                    <Unity unityContext= {this.unityContext} />
                </div>
                <div className = "accountMoneyBetPosition">
                    <AccountBalance name="Your Funds" textClass="account-balance-ingame" parent={this}></AccountBalance>
                </div>

                <div className = {(this.state.betTeam === "")? "betInfoVisibility betInfoPosition" : "betInfoPosition"}>
                    {this.state.betTeam} 
                    <br/>
                    {this.state.betMoney}
                </div>

                <div id="betForm" className="playSessionPosition betState" >
                    <div className="betMenu-item inputLog">
                        <input className="password" type="text" name="" onChange={(e)=> this.verifyNumeric(e)} />
                        <label>Money To Bet</label>
                    </div>
                    <div className="betMenu-item">
                        <label className="betMenu-item-label">Team</label>
                        <select className="betMenu-item-select">
                            <option value={this.teams[0]}>{this.teams[0]}</option>
                            <option value={this.teams[1]}>{this.teams[1]}</option>
                        </select>
                    </div>
                    
                    <div className="betMenu-item">
                        <div onClick={()=> this.bet()} className="buttonPlay "><span>Bet</span></div>
                    </div>
                </div>
                <BetTimer parent = {this}></BetTimer>
            </div>
        );
    }

     isInt(n){
        return Number(n) === n && n % 1 === 0;
    }
    
     isFloat(n){
        return Number(n) === n && n % 1 !== 0;
    }

}

export default UnityComp