import React, { Component } from 'react'

export class Alert extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             styleClasses : "alert fadeinShake " + this.props.alertColor
        }
    }
    
    
    render() {
        return (
            <div className={this.state.styleClasses}>
                    {this.props.alertMessage}
            </div>
        )
    }
}

export default Alert
