import React, { Component } from 'react'
import UnityComp from './UnityComp';
import UnityFE from './UnityFE';
import MainHeader from './MainHeader'
import Login from './Login'
import Signin from './SignIn'
import Alert from './Alert'
import Logo from './Images/fire.png'
import EventsWelcome from './EventsWelcome'
import AccountInfo from './AccountInfo'
import Credits from './Credits'




class MainRender extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
            aboveMenu: "", 
            messageForAlert: "test alert",
            canRenderAlert: false,
            colorForAlert: "alert-message-color",
            contentPage: "logo",
            team1NextGame : 1,
            team2NextGame : 2,
            nameTeam1NextGame : "",
            nameTeam2NextGame : "",
            nextGameName : "null", 
            isWebApp: true,
            unityLoading : false
        }

        this.callAboveMenu = this.callAboveMenu.bind(this)
        this.pointerAssign = this.pointerAssign.bind(this)
        this.verifyStatePointer = this.verifyStatePointer.bind(this)
        this.callAlert= this.callAlert.bind(this)
        this.mainContentRender = this.mainContentRender.bind(this)
        this.hearderThis = null;
        this.aboveMenuThis = null;
        this.alertTimeout = null;
        this.setupGame = this.setupGame.bind(this)
        this.creditsClicked = this.creditsClicked.bind(this)
  }
    
    componentDidMount(){
        this.mainContentRender()

        var viewport = document.querySelector('meta[name="viewport"]');

        if ( viewport ) {
           viewport.content = "initial-scale=0.5";
          // viewport.content = "width=1200";
        }
    }

    render() {
        return (
            <div>
                
                {this.webappOrCredits(this.state.isWebApp)}

            </div>
        )
    }

    webappOrCredits(isWebApp){
      if(isWebApp){
        return(
          <div>
              <MainHeader changeMenu={this.callAboveMenu} pointerToAssign={this.pointerAssign} parent={this}></MainHeader>
                
                {this.renderAboveWindow(this.state.aboveMenu)}
                
                {this.mainContentRender(this.state.contentPage)}

                <img className="logo" src={Logo} alt="Logo"/>

                {this.renderAlert(this.state.canRenderAlert, this.state.colorForAlert, this.state.messageForAlert)}

                <footer className="fixed-footer">
                  <a href="#0" onClick={this.creditsClicked}>
                    Credits
                  </a>
                </footer>
          </div>
        )
      }else{
        return(
          <Credits parent = {this}></Credits>
        )
      }
    }

    creditsClicked(){
      this.changeWebAppOrCredits(false)
    }

    changeWebAppOrCredits(isSetWebApp){
      this.setState({
        isWebApp: isSetWebApp
      })
    }


    mainContentRender(page){
        switch(page){
            case "logo":
                
              return(
                <div>
                    
                </div>
              )
              

              case "games":
                return(
                    <div>
                        <EventsWelcome parent={this}></EventsWelcome>
                    </div>
                  )
                

            case "newGame":

                    return(
                      <div>
                          <p className="unity-loading" hidden={this.state.unityLoading === false}>Loading...<div class="loader"></div></p>
                          <UnityFE parent={this} team1={this.state.team1NextGame} team2={this.state.team2NextGame} teamName1={this.state.nameTeam1NextGame} teamName2={this.state.nameTeam2NextGame} gameName={this.state.nextGameName}></UnityFE> 
                          <UnityComp parent={this}  team1={this.state.team1NextGame} team2={this.state.team2NextGame} teamName1={this.state.nameTeam1NextGame} teamName2={this.state.nameTeam2NextGame} gameName={this.state.nextGameName} ></UnityComp>
                      </div>
                    )
               

              default:
                return(
                    <div>
                       
                    </div>
                    )
            }
    }

    renderAboveWindow(windowType){
        switch(windowType){
          case "":
            return(
            <div>
    
            </div>
            )
           
            case "login":
            return(
            <Login pointerToAssign={this.pointerAssign} parent={this}></Login>
            )
            
            case "sign":
            return(
            <Signin pointerToAssign={this.pointerAssign} parent={this}></Signin>
            )
           
            case "account":
              return(
                <AccountInfo pointerToAssign={this.pointerAssign} parent={this}></AccountInfo>
              )

            default:
                return(
                  <div>
                      
                  </div>
                  )
    
        }
      }

      callAlert(type, message){
        this.setState({
            messageForAlert:  message ,
            canRenderAlert: true,
            colorForAlert: (type === "warning")?  "alert-warning-color" :  "alert-message-color"
        }, this.dismissAlert(5000, this))
      }

      dismissAlert(time, caller){
        if(caller.alertTimeout != null){
            clearTimeout(caller.alertTimeout)
        }

        caller.alertTimeout = setTimeout(() => {
                                        caller.finishAlert(caller)
                                    }, time)
      }

      finishAlert(caller){
            caller.setState({
                canRenderAlert: false
            })
        }

      renderAlert(canRender,color, message){
            return (canRender)? (<Alert alertMessage={message} alertColor={color} parent={this}></Alert>):(<div></div>)
      }

      callAboveMenu(windowType){
            this.setState({aboveMenu: windowType})
      }

      pointerAssign(componentDescriber,pointer){
            switch(componentDescriber){
                case "aboveMenu":
                    this.aboveMenuThis = pointer
                    break;
                case "header":
                    this.hearderThis = pointer
                break;
                default:
                  return(
                      <div>
                        
                      </div>
                      )
            }
            this.verifyStatePointer()
            
      }

      verifyStatePointer(){
          if(this.aboveMenuThis != null && this.hearderThis != null){   
            this.aboveMenuThis.assignForClose(this.hearderThis)
          }
      }

      setupGame(team1, team2, team1Name, team2Name, gameName){
        this.setState({
            team1NextGame : team1,
            team2NextGame : team2,
            nameTeam1NextGame : team1Name,
            nameTeam2NextGame : team2Name, 
            nextGameName : gameName 
        }, ()=>this.openGame(this))
      }

      openGame(pointerClass){
        this.setState({contentPage: "newGame",
          unityLoading : true
        })
      }

      unityDoneLoading(){
        this.setState({
          unityLoading : false
        })
      }
    
}

export default MainRender
