import React, {Component} from 'react';
import $ from 'jquery';
import Axius from 'axios'
import Cookies from 'universal-cookie'
var vars = require('./GlobalVars')

class UnityFE extends Component{
    constructor(props) {
        super(props)
    
        this.state = {
             
        }

        this.canMakeTransations = true
        this.cookies = new Cookies()
    }
    
    
    componentDidMount = () =>{
        console.log("Runs client side");

        
        function setViewPort(width, height, iteration=0){
            var unityViewportID = '#unityLocation canvas';
            //var unityBackground = '#__ReactUnityWebGL_'+iteration+'__';
            setTimeout(function(){
        
                if($(unityViewportID) === null || $(unityViewportID) === undefined ||
                $(unityViewportID).width() === null || $(unityViewportID).width() === undefined){
                    let newIteration = (iteration > 12) ? 0 : (iteration + 1);
                    setViewPort(width, height, newIteration);
                }else{
                    $(unityViewportID).addClass("unityViewport");
                    //$(unityBackground).addClass("unityBackground"); 
                    //foundUnityID = unityViewportID;
                    //updateViewPortSize(width, height);
                    //window.addEventListener('resize', sizeViewPort);

                }
            },300);
        }

        let screenW = giveScreenDimension(true);
        let screenH = giveScreenDimension(false);
        setViewPort(screenW,screenH);

        

        function giveScreenDimension(isWidth){
            let isWidthBigger = window.innerWidth > window.innerHeight;
            let screenW = (isWidthBigger) ? (window.innerWidth * 0.10 + 260) : (window.innerWidth * 0.4 + 260);
            let screenH = window.innerHeight * 0.38;

            return (isWidth) ? screenW : screenH
        }

       
        let form_data = new FormData()

                let body = {"IDSession": this.cookies.get('session'), "user" : this.cookies.get('user'), "game": this.props.gameName}
                
                for(var key in body){
                    form_data.append(key, body[key])
                }
                

                Axius.post(vars.default.APIServiceURL + "/signup/game", form_data).then(response => {
                    console.log(response)    
                    if(typeof(response.data) != "string" || response.data.includes("User signed to game") === false){
                        this.canMakeTransations = false
                    }
                    
                }).catch(error => {
                    console.log("Error : " + error)
                    this.canMakeTransations = false
                })
    }
    
    render(){
        return(
            <div value="This component is to controll unity gameplay...">
            </div>
        );
    }
}

export default UnityFE