import React, { PureComponent } from 'react'

export default class BetTimer extends PureComponent {
    
    constructor(props) {
        super(props)
    
        this.state = {
             timeLeft : 0,
             remainingPathColor : "green",
             pathStrokePercent : 0
        }

        this.totalTime = 0;
        
    }

    componentDidMount(){
        //this.useTimer(10, true);
        this.props.parent.timerCallerComponent = this;
    }


    useTimer(timeToReduce, isFirstDisplay, doAfter){
        this.setTimeText(timeToReduce, isFirstDisplay)
        if(timeToReduce > -1){
            setTimeout(() => {
                this.useTimer(timeToReduce-1, false, doAfter)
            }, 1000);
        }else{
            doAfter();
        }
    }


    setTimeText(timeText, isFirstDisplay){
        this.totalTime = isFirstDisplay ? timeText: this.totalTime
        this.setState({timeLeft : ((timeText < 0) ? 0 : timeText), pathStrokePercent : this.getTimeCirclePercent(timeText)})
    }

    getTimeCirclePercent(currentTime){
        var timePercent = 283 * currentTime
        timePercent = timePercent / this.totalTime
        
        return timePercent
    }

    render() {
        
       
        
        return (
            <div className="timer-position">
                <div className="base-timer">
                <svg className="base-timer__svg" viewBox="0 0 100 100" xmlns="http://www.w3.org/2000/svg">
                    <g className="base-timer__circle">
                        <circle className="base-timer__path-elapsed" cx="50" cy="50" r="45" />
                        <path id="base-timer-path-remaining" strokeDasharray={this.state.pathStrokePercent + " 283"} className={"base-timer__path-remaining " + this.state.remainingPathColor} d="M 50, 50 m -45, 0 a 45,45 0 1,0 90,0 a 45,45 0 1,0 -90,0">
                        </path>
                    </g>
                </svg>
                    <span className="timeleftLocation">
                        {this.state.timeLeft}
                    </span>
                </div>
            </div>
        )
    }
}
