import React, { Component } from 'react'

export class AccountInfoBetRow extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             
        }

        this.returnToGamesInfo = this.returnToGamesInfo.bind(this)
    }
    
    render() {
        return (
            <tr>
                <td>{this.props.money}</td>
                <td>{this.props.stage}</td>
                <td><input type="button" onClick={this.returnToGamesInfo}></input></td>  
            </tr>
        )
    }

    returnToGamesInfo(){
        this.props.parent.changeDisplay(true, this.props.gameName)
    }
}

export default AccountInfoBetRow
