import React, { Component } from 'react'
import Title from './Title'
import $ from 'jquery'
import Axius from 'axios'
import Cookies from 'universal-cookie'
import GamesInfo from './GamesInfo'
import BetsInfo from './BetsInfo'
var vars = require('./GlobalVars')

export class AccountInfo extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             games: {},
             betsForGame: [],
             userSettings: {},
             displayGames: true
        }

        this.assignForClose = this.assignForClose.bind(this)
        this.closeThisMenu = this.closeThisMenu.bind(this)

        this.cookies = new Cookies()
    }

    componentDidMount(){
        this.props.pointerToAssign("aboveMenu",this) 

        let body = {"IDSession": this.cookies.get("session")}
        let form_data = new FormData()

        for(var key in body){
            form_data.append(key, body[key])
        }
        var pointerClass = this
       
        Axius.post(vars.default.APIServiceURL + "/user/info", form_data).then(response => {
            console.log(response)    
            pointerClass.setState({
                games: response.data["games"],
                userSettings: response.data["userSettings"]
            });
            
        }).catch(error => {
            console.log("Error : " + error)
            pointerClass.props.parent.callAlert("warning", "Data or Network Failed!")
        })
    }

    assignForClose(pointer){
        this.headerThis= pointer
    }

    showHeaderButtons(showLoginButtons){
        if(showLoginButtons){
            this.headerThis.setState({showButtons: true})
        }else{
            
            this.headerThis.setState({showUserButtons: true, userName: this.cookies.get('user')})
        }
        this.headerThis.toDefaultMenu()
    }
    
    render() {
        return (
            <div id="aboveWindow" className="logIntoService combMoveFadeDown">
                <Title title="Account Info"></Title>
                <div className="accountInfoWinInfo">
                    {(this.state.userSettings.user !== undefined)? this.state.userSettings.user : ""}
                    <br/>
                    {(this.state.userSettings.email !== undefined)? this.state.userSettings.email : ""}
                    <br/>
                    {(this.state.userSettings.money !== undefined)? this.state.userSettings.money : ""}
                </div>
                
                {this.renderGamesOrBets(this.state.displayGames)}
                <div onClick={()=> this.closeThisMenu(false)} className="close"></div>
            </div>
        )
    }

    renderGamesOrBets(isGames){
        if(isGames){
            return(
                <GamesInfo games = {this.state.games} parent = {this}></GamesInfo>    
            )
        }
        else{
            return(
                <BetsInfo bets = {this.state.betsForGame} parent = {this}></BetsInfo>
            )
        }
    }

    changeDisplay(isDisplayingGames, gameForRequest){
        
        if(isDisplayingGames === false){
            let body = {"IDSession": this.cookies.get("session"), "game": gameForRequest}
            let form_data = new FormData()

            for(var key in body){
                form_data.append(key, body[key])
            }
            var pointerClass = this
        
            Axius.post(vars.default.APIServiceURL + "/user/game/info", form_data).then(response => {
                console.log(response)    
                pointerClass.setState({
                    betsForGame: response.data["bets"],
                    displayGames: isDisplayingGames
                });
                
            }).catch(error => {
                console.log("Error : " + error)
                pointerClass.props.parent.callAlert("warning", "Data or Network Failed!")
            })
        }else{
            this.setState({
                displayGames: isDisplayingGames
            })
        }


    }

    closeThisMenu(showLoginButtons){
        var pointerClass = this;
        $("#aboveWindow").removeClass("combMoveFadeDown")
        $("#aboveWindow").addClass("combMoveFadeUp")
        setTimeout(function(){
            pointerClass.showHeaderButtons(showLoginButtons)
        }, 550)
    }
}

export default AccountInfo
