import React, { Component } from 'react'

export class AccountInfoGameRow extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
             
        }

        this.gameClicked = this.gameClicked.bind(this)
    }
    
    render() {
        return (
            <tr>
                <td>{this.props.team1}</td>
                <td>{this.props.team2}</td>
                <td>{this.props.money}</td>
                <td><input type="button" onClick={this.gameClicked}></input></td>  
            </tr>
        )
    }

    gameClicked(){
        this.props.parent.changeDisplay(false, this.props.gameName)
    }
}

export default AccountInfoGameRow
