import React, { Component } from 'react'
import Axius from 'axios'
import Cookies from 'universal-cookie'
var vars = require('./GlobalVars')

export class AccountBalance extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            userMoney : 0
        }

        this.cookies = new Cookies()
    }
    
    componentDidMount(){
        this.renderAcountBalance()
        var pointerClass = this
            
        
        if(pointerClass.props.parent.account !== undefined){
            pointerClass.props.parent.account = this
        }
    }

    renderAcountBalance(){

        let form_data = new FormData()

        let body = {"IDSession": this.cookies.get('session'), "user" : this.cookies.get('user')}
        
        for(var key in body){
            form_data.append(key, body[key])
        }
        var pointerClass = this

        Axius.post(vars.default.APIServiceURL + "/user/money", form_data).then(response => {
            console.log(response)    
            pointerClass.setState({ userMoney : response.data["money"]}) 
            
        }).catch(error => {
            console.log("Error : " + error)
        })
    }

    render() {
        return (
            <div className={this.props.textClass}>
                <div>{this.props.name} <br/>{this.state.userMoney}</div>
            </div>
        )
    }
}

export default AccountBalance

