import React, { Component } from 'react'
import Title from './Title'
import $ from 'jquery'
import Axius from 'axios'
import Cookies from 'universal-cookie'
var vars = require('./GlobalVars')

class Login extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            
        }
        
        
        this.assignForClose = this.assignForClose.bind(this)
        this.closeThisMenu = this.closeThisMenu.bind(this)
        this.showHeaderButtons = this.showHeaderButtons.bind(this)
        this.tryToLogin= this.tryToLogin.bind(this)
        this.headerThis = null
        this.cookies = new Cookies()
    }
    

    componentDidMount(){
        this.props.pointerToAssign("aboveMenu",this) 
    }

    render() {
        return (
            <div id="aboveWindow" className="logIntoService combMoveFadeDown">
                <Title title="Login"></Title>
                <form autoComplete="new-password">
                        <div className="inputLog"> 
                            <input className="userMail" type="text" name="" required=""/>
                            <label>Username Or Email</label>
                        </div>
                        <div className="inputLog">
                            <input className="password" type="password" name="" required=""/>
                            <label>Password</label>
                        </div>
                        <button type="button" onClick={()=>this.tryToLogin()} id="loginProcess"className="mainButton">
                            Login
                        </button>
                        <div onClick={()=> this.closeThisMenu(true)} className="close"></div>
                </form>
            </div>
        )
    }

    tryToLogin(){
        var loginMailOrUser = $("#aboveWindow input.userMail").val()
        let password = $("#aboveWindow input.password").val()

        if(loginMailOrUser.replaceAll(" ", "") !== "" && password.replaceAll(" ", "") !== ""){
            let typeLogin = (loginMailOrUser.includes("@") && loginMailOrUser.includes("."))? "email" : "user"
            let body = {[typeLogin]: loginMailOrUser, "password":password }
            let form_data = new FormData()

            for(var key in body){
                form_data.append(key, body[key])
            }
            var pointerClass = this
        
            Axius.post(vars.default.APIServiceURL + "/login", form_data).then(response => {
                console.log(response)    
                if(typeof(response.data) == "string" && response.data.includes("Login is not valid")){
                    pointerClass.props.parent.callAlert("warning", "Login Failed!")
                }else if(typeof(response.data["IDSession"]) == "string"){
                    pointerClass.props.parent.callAlert("normal", "Login Success!")
                    this.cookies.set('session', response.data["IDSession"])
                    this.cookies.set('user', loginMailOrUser)
                    console.log(this.cookies.get('session'))
                    pointerClass.closeThisMenu(false);
                    pointerClass.props.parent.setState({contentPage: "games"})
                }
                else{
                    pointerClass.props.parent.callAlert("warning", "Login Went Wrong!")
                }
                
            }).catch(error => {
                console.log("Error : " + error)
                pointerClass.props.parent.callAlert("warning", "Login or Network Failed!")
            })
        }else{
            this.props.parent.callAlert("warning", "Not All Fields Are Filled!")
        }

        

    }

    

    assignForClose(pointer){
        this.headerThis= pointer
    }

    showHeaderButtons(showLoginButtons){
            if(showLoginButtons){
                this.headerThis.setState({showButtons: true})
            }else{
                
                this.headerThis.setState({showUserButtons: true, userName: this.cookies.get('user')})
            }
            this.headerThis.toDefaultMenu()
    }

    closeThisMenu(showLoginButtons){
        var pointerClass = this;
        $("#aboveWindow").removeClass("combMoveFadeDown")
        $("#aboveWindow").addClass("combMoveFadeUp")
        setTimeout(function(){
            pointerClass.showHeaderButtons(showLoginButtons)
        }, 550)
    }
}

export default Login
