import React, { Component } from 'react'
import AccountInfoBetRow from './AccountInfoBetRow'

export class BetsInfo extends Component {
    render() {
        return (
            <div className="accountInfoWinGames">
                    <table>
                        <thead>
                            <tr>
                                <th>Money</th>
                                <th>Round</th>
                            </tr>
                        </thead>
                        <tbody>
                           
                            {Object.keys(this.props.bets).map( (index) => { 
                                        return( 
                                            <AccountInfoBetRow parent={this.props.parent} key={index} stage={this.props.bets[index]["stage"]} money={this.props.bets[index]["money"]}>
                                            </AccountInfoBetRow>
                                        )
                                    }
                                )
                            }
                        </tbody>
                    </table>
                </div> 
        )
    }
}

export default BetsInfo
